<template>
  <div v-if="$route.query.method == '北京市A类定点医院'" class="medicalServiceDetail">
    <div class="title">北京市基本医疗保险A类定点医疗机构名单</div>
    <pre class="content-text"><div><p style="margin-top:0pt; margin-bottom:0pt; text-indent:21pt; line-height:18pt"><span style="font-family:'宋体'; font-size:10pt; color:#404040; ">1、中国医学科学院北京协和医院</span></p><p style="margin-top:0pt; margin-bottom:0pt; text-indent:21pt; line-height:18pt"><span style="font-family:'宋体'; font-size:10pt; color:#404040; ">2、首都医科大学附属北京同仁医院</span></p><p style="margin-top:0pt; margin-bottom:0pt; text-indent:21pt; line-height:18pt"><span style="font-family:'宋体'; font-size:10pt; color:#404040; ">3、首都医科大学宣武医院</span></p><p style="margin-top:0pt; margin-bottom:0pt; text-indent:21pt; line-height:18pt"><span style="font-family:'宋体'; font-size:10pt; color:#404040; ">4、首都医科大学附属北京友谊医院</span></p><p style="margin-top:0pt; margin-bottom:0pt; text-indent:21pt; line-height:18pt"><span style="font-family:'宋体'; font-size:10pt; color:#404040; ">5、北京大学第一医院</span></p><p style="margin-top:0pt; margin-bottom:0pt; text-indent:21pt; line-height:18pt"><span style="font-family:'宋体'; font-size:10pt; color:#404040; ">6、北京大学人民医院</span></p><p style="margin-top:0pt; margin-bottom:0pt; text-indent:21pt; line-height:18pt"><span style="font-family:'宋体'; font-size:10pt; color:#404040; ">7、北京大学第三医院</span></p><p style="margin-top:0pt; margin-bottom:0pt; text-indent:21pt; line-height:18pt"><span style="font-family:'宋体'; font-size:10pt; color:#404040; ">8、北京积水潭医院</span></p><p style="margin-top:0pt; margin-bottom:0pt; text-indent:21pt; line-height:18pt"><span style="font-family:'宋体'; font-size:10pt; color:#404040; ">9、中国中医科学院广安门医院</span></p><p style="margin-top:0pt; margin-bottom:0pt; text-indent:21pt; line-height:18pt"><span style="font-family:'宋体'; font-size:10pt; color:#404040; ">10、首都医科大学附属北京朝阳医院</span></p><p style="margin-top:0pt; margin-bottom:0pt; text-indent:21pt; line-height:18pt"><span style="font-family:'宋体'; font-size:10pt; color:#404040; ">11、中日友好医院</span></p><p style="margin-top:0pt; margin-bottom:0pt; text-indent:21pt; line-height:18pt"><span style="font-family:'宋体'; font-size:10pt; color:#404040; ">12、北京大学首钢医院</span></p><p style="margin-top:0pt; margin-bottom:0pt; text-indent:21pt; line-height:18pt"><span style="font-family:'宋体'; font-size:10pt; color:#404040; ">13、首都医科大学附属北京中医医院</span></p><p style="margin-top:0pt; margin-bottom:0pt; text-indent:21pt; line-height:18pt"><span style="font-family:'宋体'; font-size:10pt; color:#404040; ">14、首都医科大学附属北京天坛医院</span></p><p style="margin-top:0pt; margin-bottom:0pt; text-indent:21pt; line-height:18pt"><span style="font-family:'宋体'; font-size:10pt; color:#404040; ">15、北京世纪坛医院(北京铁路总医院)</span></p><p style="margin-top:0pt; margin-bottom:0pt; text-indent:21pt; line-height:18pt"><span style="font-family:'宋体'; font-size:10pt; color:#404040; ">16、北京市健宫医院</span></p><p style="margin-top:0pt; margin-bottom:0pt; text-indent:21pt; line-height:18pt"><span style="font-family:'宋体'; font-size:10pt; color:#404040; ">17、北京市房山区良乡医院</span></p><p style="margin-top:0pt; margin-bottom:0pt; text-indent:21pt; line-height:18pt"><span style="font-family:'宋体'; font-size:10pt; color:#404040; ">18、北京市大兴区人民医院</span></p><p style="margin-top:0pt; margin-bottom:0pt; text-indent:21pt; line-height:18pt"><span style="font-family:'宋体'; font-size:10pt; color:#404040; ">19、北京市石景山医院</span></p></div></pre>
  </div>
  <div v-else-if="$route.query.method == '机关报销人员医疗须知'" class="medicalServiceDetail">
    <div class="title">机关报销离退休人员医疗须知</div>
    <pre class="content-text"><div data-v-26c94859=""><p data-v-26c94859="" style="margin-top: 0pt; margin-bottom: 0pt; text-indent: 23pt; text-align: justify; line-height: 18pt;"><span data-v-26c94859="" style="font-family: 黑体; font-size: 10pt;">一</span><span data-v-26c94859="" style="font-family: 黑体; font-size: 10pt;">、定点医院范围及报销标准</span></p><p data-v-26c94859="" style="margin-top: 0pt; margin-bottom: 0pt; text-indent: 23pt; text-align: justify; line-height: 18pt;"><span data-v-26c94859="" style="font-family: 仿宋_GB2312; font-size: 10pt;">在机关（离退休干部局财务）报销的离退休老同志，</span><span data-v-26c94859="" style="font-family: 仿宋_GB2312; font-size: 10pt;">除合同医院（北大医院）外，可在</span><span data-v-26c94859="" style="font-family: 仿宋_GB2312; font-size: 10pt;">所选定的</span><span data-v-26c94859="" style="font-family: 仿宋_GB2312; font-size: 10pt;">就近医院和社区医院就诊。</span></p><p data-v-26c94859="" style="margin-top: 0pt; margin-bottom: 0pt; text-indent: 23pt; text-align: justify; line-height: 18pt;"><span data-v-26c94859="" style="font-family: 仿宋_GB2312; font-size: 10pt;">医保范围内的医疗费按100%比例报销。持卫健委（原卫生部）发医疗证</span><span data-v-26c94859="" style="font-family: 仿宋_GB2312; font-size: 10pt;">的局级离退休人员</span><span data-v-26c94859="" style="font-family: 仿宋_GB2312; font-size: 10pt;">，</span><span data-v-26c94859="" style="font-family: 仿宋_GB2312; font-size: 10pt;">可在</span><span data-v-26c94859="" style="font-family: 仿宋_GB2312; font-size: 10pt;">合同医院享受高干门诊</span><span data-v-26c94859="" style="font-family: 仿宋_GB2312; font-size: 10pt;">和</span><span data-v-26c94859="" style="font-family: 仿宋_GB2312; font-size: 10pt;">住院标准。</span></p><p data-v-26c94859="" style="margin-top: 0pt; margin-bottom: 0pt; text-indent: 23pt; text-align: justify; line-height: 18pt;"><span data-v-26c94859="" style="font-family: 黑体; font-size: 10pt;">二、定点</span><span data-v-26c94859="" style="font-family: 黑体; font-size: 10pt;">医院变更流程</span></p><p data-v-26c94859="" style="margin-top: 0pt; margin-bottom: 0pt; text-indent: 23pt; text-align: justify; line-height: 18pt;"><span data-v-26c94859="" style="font-family: 仿宋_GB2312; font-size: 10pt;">申请变更</span><span data-v-26c94859="" style="font-family: 仿宋_GB2312; font-size: 10pt;">北京市</span><span data-v-26c94859="" style="font-family: 仿宋_GB2312; font-size: 10pt;">内定点医疗机构，报</span><span data-v-26c94859="" style="font-family: 仿宋_GB2312; font-size: 10pt;">局</span><span data-v-26c94859="" style="font-family: 仿宋_GB2312; font-size: 10pt;">二处登记</span><span data-v-26c94859="" style="font-family: 仿宋_GB2312; font-size: 10pt;">；</span><span data-v-26c94859="" style="font-family: 仿宋_GB2312; font-size: 10pt;">申请京外就医，本人提交书面申请，报</span><span data-v-26c94859="" style="font-family: 仿宋_GB2312; font-size: 10pt;">局</span><span data-v-26c94859="" style="font-family: 仿宋_GB2312; font-size: 10pt;">二处登记备案。</span></p><p data-v-26c94859="" style="margin-top: 0pt; margin-bottom: 0pt; text-indent: 23pt; text-align: justify; line-height: 18pt;"><span data-v-26c94859="" style="font-family: 仿宋_GB2312; font-size: 10pt;">北京市医保定点医院及编码可登录北京市医疗保障局官方网站</span><span data-v-26c94859="" style="font-family: 仿宋_GB2312; font-size: 10pt;">http://ybj.beijing.gov.cn/</span><span data-v-26c94859="" style="font-family: 仿宋_GB2312; font-size: 10pt;">查询。</span></p><p data-v-26c94859="" style="margin-top: 0pt; margin-bottom: 0pt; text-indent: 23pt; text-align: justify; line-height: 18pt;"><span data-v-26c94859="" style="font-family: 黑体; font-size: 10pt;">三、</span><span data-v-26c94859="" style="font-family: 黑体; font-size: 10pt;">医药费</span><span data-v-26c94859="" style="font-family: 黑体; font-size: 10pt;">报销流程</span></p><p data-v-26c94859="" style="margin-top: 0pt; margin-bottom: 0pt; text-indent: 23pt; text-align: justify; line-height: 18pt;"><span data-v-26c94859="" style="font-family: 仿宋_GB2312; font-size: 10pt;">在定点医院</span><span data-v-26c94859="" style="font-family: 仿宋_GB2312; font-size: 10pt;">门诊就医，自行垫付医药费，持有关票据到局财务室报销。</span></p><p data-v-26c94859="" style="margin-top: 0pt; margin-bottom: 0pt; text-indent: 23pt; text-align: justify; line-height: 18pt;"><span data-v-26c94859="" style="font-family: 仿宋_GB2312; font-size: 10pt;">在定点医院</span><span data-v-26c94859="" style="font-family: 仿宋_GB2312; font-size: 10pt;">住院</span><span data-v-26c94859="" style="font-family: 仿宋_GB2312; font-size: 10pt;">，</span><span data-v-26c94859="" style="font-family: 仿宋_GB2312; font-size: 10pt;">可自行垫付住院费，或持医院开具的住院通知单（2</span><span data-v-26c94859="" style="font-family: 仿宋_GB2312; font-size: 10pt;">-10</span><span data-v-26c94859="" style="font-family: 仿宋_GB2312; font-size: 10pt;">万元</span><span data-v-26c94859="" style="font-family: 仿宋_GB2312; font-size: 10pt;">，盖章</span><span data-v-26c94859="" style="font-family: 仿宋_GB2312; font-size: 10pt;">）到</span><span data-v-26c94859="" style="font-family: 仿宋_GB2312; font-size: 10pt;">局</span><span data-v-26c94859="" style="font-family: 仿宋_GB2312; font-size: 10pt;">二处申请领取住院费支票，出院后持医院打印的治疗费用明细单、收据、诊断书等到</span><span data-v-26c94859="" style="font-family: 仿宋_GB2312; font-size: 10pt;">局</span><span data-v-26c94859="" style="font-family: 仿宋_GB2312; font-size: 10pt;">财务室报销。</span></p><div data-v-26c94859=""><p data-v-26c94859="" style="margin-top: 0pt; margin-bottom: 0pt; font-size: 9pt;"><span data-v-26c94859="" style="height: 0pt; display: block; position: absolute; z-index: -65536;"><img data-v-26c94859="" src="aaln3-tug7s.001.png" width="6" height="14" alt="" style="margin-left: 205.4pt; position: absolute;"></span><span data-v-26c94859="" style="font-family: &quot;Times New Roman&quot;;">&nbsp;</span></p></div></div></pre>
  </div>
  <div v-else-if="$route.query.method == '医疗照顾人员医疗须知'" class="medicalServiceDetail">
    <div class="title">离退休医疗照顾人员（持卡）医疗须知</div>
    <pre class="content-text"><div><p style="margin-top:0pt; margin-bottom:0pt; text-indent:21pt; text-align:justify; line-height: 18pt; widows:0; orphans:0"><span style="font-family:'黑体'; font-size: 10pt">一、医疗</span><span style="font-family:'黑体'; font-size: 10pt">照顾人员范围</span></p><p style="margin-top:0pt; margin-bottom:0pt; text-indent:21pt; text-align:justify; line-height: 18pt; widows:0; orphans:0"><span style="font-family:'仿宋_GB2312'; font-size: 10pt">1.部级以及享受部级医疗待遇离退休老同志</span></p><p style="margin-top:0pt; margin-bottom:0pt; text-indent:21pt; text-align:justify; line-height: 18pt; widows:0; orphans:0"><span style="font-family:'仿宋_GB2312'; font-size: 10pt">2.</span><span style="font-family:'仿宋_GB2312'; font-size: 10pt">1996年以前为局级实职干部</span></p><p style="margin-top:0pt; margin-bottom:0pt; text-indent:21pt; text-align:justify; line-height: 18pt; widows:0; orphans:0"><span style="font-family:'黑体'; font-size: 10pt">二、报销方式</span></p><p style="margin-top:0pt; margin-bottom:0pt; text-indent:21pt; text-align:justify; line-height: 18pt; widows:0; orphans:0"><span style="font-family:'仿宋_GB2312'; font-size: 10pt">持</span><span style="font-family:'仿宋_GB2312'; font-size: 10pt">单位办理的</span><span style="font-family:'仿宋_GB2312'; font-size: 10pt">社保卡</span><span style="font-family:'仿宋_GB2312'; font-size: 10pt">刷卡</span><span style="font-family:'仿宋_GB2312'; font-size: 10pt">就医，医疗费用实时结算。医疗费用报销标准按照有关规定执行。</span></p><p style="margin-top:0pt; margin-bottom:0pt; text-indent:21pt; text-align:justify; line-height: 18pt; widows:0; orphans:0"><span style="font-family:'黑体'; font-size: 10pt">三、定点医院范围</span></p><p style="margin-top:0pt; margin-bottom:0pt; text-indent:21pt; text-align:justify; line-height: 18pt; widows:0; orphans:0"><span style="font-family:'仿宋_GB2312'; font-size: 10pt">1.正部级及享受正部级医疗待遇老同志</span></p><p style="margin-top:0pt; margin-bottom:0pt; text-indent:21pt; text-align:justify; line-height: 18pt; widows:0; orphans:0"><span style="font-family:'仿宋_GB2312'; font-size: 10pt">可选医院为：在非军队系统的中央保健基地医院中选择1家合同医院办理医疗证，进行就诊和年度体检，并可选择1至2家就近医院和指定基层医疗机构，经中央保健办批准备案后就近门诊，需在就近医院机构就诊时，在普通门诊就诊。可持卡在本市A类医院、专科医院和中医医院直接就诊。</span></p><p style="margin-top:0pt; margin-bottom:0pt; text-indent:21pt; text-align:justify; line-height: 18pt; widows:0; orphans:0"><span style="font-family:'仿宋_GB2312'; font-size: 10pt">2.</span><span style="font-family:'仿宋_GB2312'; font-size: 10pt">副部级及享受副部级医疗待遇老同志</span></p><p style="margin-top:0pt; margin-bottom:0pt; text-indent:21pt; text-align:justify; line-height: 18pt; widows:0; orphans:0"><span style="font-family:'仿宋_GB2312'; font-size: 10pt">可选医院为：在1家指定合同医院办理副部级干部医疗证，享受医疗照顾服务，进行年度体检，并可选择1至2家就近医院和指定基层医疗机构就诊，需在就近医院机构就诊时，在普通门诊就诊。可持卡在本市A类医院、专科医院和中医医院直接就诊。</span></p><p style="margin-top:0pt; margin-bottom:0pt; text-indent:21pt; text-align:justify; line-height: 18pt; widows:0; orphans:0"><span style="font-family:'仿宋_GB2312'; font-size: 10pt">3.</span><span style="font-family:'仿宋_GB2312'; font-size: 10pt">享受按部级标准报销医疗费待遇老同志</span></p><p style="margin-top:0pt; margin-bottom:0pt; text-indent:21pt; text-align:justify; line-height: 18pt; widows:0; orphans:0"><span style="font-family:'仿宋_GB2312'; font-size: 10pt">按部级标准报销医疗费待遇指：医疗费报销标准与部级待遇报销标准相同；门诊和住院照顾标准仍按原医疗待遇标准执行，病床费按原待遇标准报销；合同医院原则上不作变更。</span></p><p style="margin-top:0pt; margin-bottom:0pt; text-indent:21pt; text-align:justify; line-height: 18pt; widows:0; orphans:0"><span style="font-family:'仿宋_GB2312'; font-size: 10pt">可选医院为：1家合同医院、2家就近医院、1家社区医院以及本市A类医院、专科医院和中医医院。</span></p><p style="margin-top:0pt; margin-bottom:0pt; text-indent:21pt; text-align:justify; line-height: 18pt; widows:0; orphans:0"><span style="font-family:'仿宋_GB2312'; font-size: 10pt">4</span><span style="font-family:'仿宋_GB2312'; font-size: 10pt">.享受医疗照顾局级离退休老同志</span></p><p style="margin-top:0pt; margin-bottom:0pt; text-indent:21pt; text-align:justify; line-height: 18pt; widows:0; orphans:0"><span style="font-family:'仿宋_GB2312'; font-size: 10pt">（1）离休局级医照人员，可选医院为：1家合同医院（北大医院）、2家就近医院、1家社区医院，并可在本市A类医院、专科医院和中医医院直接就诊。</span></p><p style="margin-top:0pt; margin-bottom:0pt; text-indent:21pt; text-align:justify; line-height: 18pt; widows:0; orphans:0"><span style="font-family:'仿宋_GB2312'; font-size: 10pt">（2）退休局级医照人员，可选医院为：1家合同医院（北大医院）、1家就近医院、1家社区医院。</span></p><p style="margin-top:0pt; margin-bottom:0pt; text-indent:21pt; text-align:justify; line-height: 18pt; widows:0; orphans:0"><span style="font-family:'黑体'; font-size: 10pt">四、定点医院更改流程</span></p><p style="margin-top:0pt; margin-bottom:0pt; text-indent:21pt; text-align:justify; line-height: 18pt; widows:0; orphans:0"><span style="font-family:'仿宋_GB2312'; font-size: 10pt">申请变更</span><span style="font-family:'仿宋_GB2312'; font-size: 10pt">北京</span><span style="font-family:'仿宋_GB2312'; font-size: 10pt">市内定点医疗机构，</span><span style="font-family:'仿宋_GB2312'; font-size: 10pt">可联系离退休干部局二处，</span><span style="font-family:'仿宋_GB2312'; font-size: 10pt">填写《北京市公费医疗直管医疗照顾人员信息登记表》，报北京市医保中心审批登记。</span></p><p style="margin-top:0pt; margin-bottom:0pt; text-indent:21pt; text-align:justify; line-height: 18pt; widows:0; orphans:0"><span style="font-family:'仿宋_GB2312'; font-size: 10pt">北京市医保定点医院及编码可登录北京市医疗保障局官方网站</span><span style="font-family:'仿宋_GB2312'; font-size: 10pt">http://ybj.beijing.gov.cn/</span><span style="font-family:'仿宋_GB2312'; font-size: 10pt">查询。</span></p><p style="margin-top:0pt; margin-bottom:0pt; text-indent:21pt; text-align:justify; line-height: 18pt; widows:0; orphans:0"><span style="font-family:'黑体'; font-size: 10pt">五、京外就医备案流程</span></p><p style="margin-top:0pt; margin-bottom:0pt; text-indent:21pt; text-align:justify; line-height: 18pt; widows:0; orphans:0"><span style="font-family:'仿宋_GB2312'; font-size: 10pt">申请京外就医，本人提交书面申请，说明在京外居住的期限（</span><span style="font-family:'仿宋_GB2312'; font-size: 10pt">起止</span><span style="font-family:'仿宋_GB2312'; font-size: 10pt">日期）以及选定的当地定点医院，填写《北京市公费医疗照顾人员异地就医定点医疗机构申报审批表》，报北京市医保中心审批备案。</span></p><p style="margin-top:0pt; margin-bottom:0pt; text-indent:21pt; text-align:justify; line-height: 18pt; widows:0; orphans:0"><span style="font-family:'仿宋_GB2312'; font-size: 10pt">备案期间，老同志不可同时在</span><span style="font-family:'仿宋_GB2312'; font-size: 10pt">北京市定点医院</span><span style="font-family:'仿宋_GB2312'; font-size: 10pt">刷卡就医，待备案期限到期后方可恢复。</span></p><p style="margin-top:0pt; margin-bottom:0pt; text-indent:21pt; text-align:justify; line-height: 18pt; widows:0; orphans:0"><span style="font-family:'黑体'; font-size: 10pt">六、非定点医院就医申请手工报销流程</span></p><p style="margin-top:0pt; margin-bottom:0pt; text-indent:21pt; text-align:justify; line-height: 18pt; widows:0; orphans:0"><span style="font-family:'仿宋_GB2312'; font-size: 10pt">在非定点医院发生的急诊费用，持有关票据到离退休干部局财务室申请手工报销，财务人员打印《北京市公费医疗照顾人员手工报销医药费报销明细表》，连同电子版表格，报北京市医保中心审核报销。</span></p><p style="margin-top:0pt; margin-bottom:0pt; text-indent:21pt; text-align:justify; line-height: 18pt; widows:0; orphans:0"><span style="font-family:'仿宋_GB2312'; font-size: 10pt">已办理京外就医备案的，在备案期间，老同志可在所选的当地定点医院就医，医药费自行垫付，回京后（不要跨年度）持相关票据（收据、处方、出院诊断书、带有标识的收费明细单等）到财务室申请手工报销，报销流程同急诊。</span></p><div style="-aw-headerfooter-type:footer-primary"><p style="margin-top:0pt; margin-bottom:0pt; widows:0; orphans:0; font-size:9pt"><span style="height:0pt; display:block; position:absolute; z-index:-65536"><img src="airxs-qxack.001.png" width="6" height="14" alt="" style="margin-left:205.4pt; -aw-left-pos:0pt; -aw-rel-hpos:margin; -aw-rel-vpos:paragraph; -aw-top-pos:0pt; -aw-wrap-type:none; position:absolute"></span><span style="font-family:'Times New Roman'">&nbsp;</span></p></div></div></pre>
  </div>
</template>

<script>
export default {
  data() {
    return {};
  }
};
</script>

<style lang="less" scoped>
.medicalServiceDetail {
  background: #f5f5f5;
  padding: 26px;
  .title {
    font-size: 34px;
    color: #333333;
    font-weight: 500;
    margin: 30px 0;
  }
  .content-text {
    font-size: 34px;
    color: #343434;
    line-height: 54px;
  }
}
</style>



